    handleFirstWheeel(x)
    {

        const element = ReactDOM.findDOMNode(this.refs.myscroll)
        const thirdScroll=ReactDOM.findDOMNode(this.refs.thirdScroll);
        const firstScroll=ReactDOM.findDOMNode(this.refs.firstScroll); 
        let diff=0;
        if(this.prevFirst>firstScroll.scrollTop)
        {
            diff=this.prevFirst-firstScroll.scrollTop;
            thirdScroll.scrollTop-=diff;
            element.scrollTop-=diff; 
        
        }
        else if(this.prevFirst==firstScroll.scrollTop)
        {
            if(x.deltaY>0)
            {
                element.scrollTop+=100;
                thirdScroll.scrollTop+=100
            }
            else
            {
                element.scrollTop-=100;
                thirdScroll.scrollTop-=100
            }
        }
        else
        {
            diff=firstScroll.scrollTop-this.prevFirst;
            element.scrollTop+=diff;
            thirdScroll.scrollTop+=diff
            
            
        }
        this.prevThird=thirdScroll.scrollTop;
        this.prevFirst=firstScroll.scrollTop;
        this.prevSecond=element.scrollTop;  

    }
